import { useEffect, useState } from 'react';
import './style.css';

export default function Notificacao({ visivel, onFechar, mensagem, imagem }) {
  const [mostrar, setMostrar] = useState(false);

  useEffect(() => {
    let timer;

    if (visivel) {
      setMostrar(true); // Mostra com animao

      timer = setTimeout(() => {
        setMostrar(false); // Inicia animao de sada
        setTimeout(() => onFechar(), 300); // Espera a animao antes de esconder de vez
      }, 3000);
    }

    return () => clearTimeout(timer);
  }, [visivel, onFechar]);

  const handleFecharClick = () => {
    setMostrar(false); // Inicia animao de sada manualmente
    setTimeout(() => onFechar(), 300); // Espera a animao e chama onFechar
  };

  if (!visivel && !mostrar) return null;

  return (
    <div className={`notificacao ${mostrar ? 'mostrar' : ''}`}>
      <article className="notificacaoHeader">
        <button className="btn-fecha" onClick={handleFecharClick}>
          <i className="fi fi-rr-cross-small"></i>
        </button>
      </article>
      <div className="notificacaoInfo">
        {imagem && <img src={imagem} alt="Notificao" />}
        <div className="notificacaoConteudo">
          <p className="themePurple">{mensagem}</p>
        </div>
      </div>
    </div>
  );
}
